<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-xs-12">
        <h1>rendicontazione</h1>
      </div>
      <div class="col-md-1 col-xs-12">
        <button
          class="btn btn-success"
          data-toggle="modal"
          data-target="#addNew"
          @click="getAdmin"
        >
          +
        </button>
      </div>
    </div>
    <div class="row">
      <div class="col-md-4">
        <button @click="changeMonth(-1)" class="btn btn-success">
          Mese Precedente
        </button>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <button @click="changeMonth(0)" class="btn btn-success">
            Mese Attuale
          </button>
        </div>
      </div>
      <div class="col-md-4">
        <button @click="changeMonth(+1)" class="btn btn-success">
          Mese Successivo
        </button>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <label for="">Data Inizio</label>
          <input
            type="date"
            class="form-control"
            :v-model="startDate"
            :value="startDate"
            @input="startDate = getDate($event.target.valueAsDate, 0)"
          />
        </div>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <label for="">Data Fine</label>
          <input
            type="date"
            class="form-control"
            :v-model="endDate"
            :value="endDate"
            @input="endDate = getDate($event.target.valueAsDate, 0)"
          />
        </div>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <label for="">User</label>
          <select v-model="userId" class="form-control" required>
            <option value="" disabled>/</option>
            <option
              v-for="(item, index) in userList"
              :key="index"
              :value="item.k"
            >
              {{ item.v }}
            </option>
          </select>
        </div>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <button @click="submit" class="btn btn-success">Aggiorna</button>
        </div>
      </div>
    </div>
    <!-- <hr /> -->
    <!-- tabs start -->
    <ul class="nav nav-tabs nav-justified">
      <li class="nav-item">
        <a
          class="nav-link"
          @click.prevent="setActive('home')"
          :class="{ active: isActive('home') }"
          href="#home"
          >Totali</a
        >
      </li>
      <li class="nav-item">
        <a
          class="nav-link"
          @click.prevent="setActive('profile')"
          :class="{ active: isActive('profile') }"
          href="#profile"
          >Dettagli</a
        >
      </li>
    </ul>
    <div class="tab-content py-3" id="myTabContent">
      <div
        class="tab-pane fade"
        :class="{ 'active show': isActive('home') }"
        id="home"
      >
        <div class="row">
          <div v-if="hasData" style="width: 100%">
            <table class="table table-striped">
              <thead>
                <th>mese</th>
                <th>cliente</th>
                <th>ore ordinario</th>
                <th>ore straordinario</th>
                <th>totale</th>
              </thead>
              <tbody>
                <tr v-for="(item, index) in data" :key="index">
                  <td>{{ item.month }}</td>
                  <td>{{ item.CLIENT }}</td>
                  <td>{{ item.h }}</td>
                  <td>{{ item.h_ex }}</td>
                  <td>{{ sum(item.h_ex, item.h) }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div
        class="tab-pane fade"
        :class="{ 'active show': isActive('profile') }"
        id="profile"
      >
        <div class="row">
          <div v-if="hasData" style="width: 100%">
            <table class="table table-striped">
              <thead>
                <th>Data</th>
                <th>Ora inizio</th>
                <th>Ora fine</th>
                <th>Cliente</th>
                <th>Progetto</th>
                <th>Descrizione</th>
                <th>Ore tot</th>
                <th>Straordinario</th>
              </thead>
              <tbody>
                <tr v-for="(item, index) in data2" :key="index">
                  <td>{{ item.date }}</td>
                  <td>{{ item.start }}</td>
                  <td>{{ item.end }}</td>
                  <td>{{ item.client }}</td>
                  <td>{{ item.project }}</td>
                  <td>{{ item.description }}</td>
                  <td>{{ item.hours }}</td>
                  <td>{{ ext(item.extraordinary) }}</td>
                </tr>
              </tbody>
              <tfoot>
                <th>Data</th>
                <th>Ora inizio</th>
                <th>Ora fine</th>
                <th>Cliente</th>
                <th>Progetto</th>
                <th>Descrizione</th>
                <th>Ore tot</th>
                <th>Straordinario</th>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>
    <!-- tabs end -->

    <!-- modal for add new record -->
    <div
      class="modal fade bd-example-modal-lg"
      id="addNew"
      tabindex="-1"
      role="dialog"
      aria-labelledby="addRecord"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="addRecord">Aggiungi</h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <h6>* Ora inizio e fine deve essere ogni 15 minuti</h6>
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label for="">Giorno</label>
                  <input
                    type="date"
                    class="form-control"
                    v-model="addData.date"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="">Ora inizio</label>
                  <input
                    type="time"
                    class="form-control"
                    v-model="addData.start"
                  />
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="">Ora fine</label>
                  <input
                    type="time"
                    class="form-control"
                    v-model="addData.end"
                    @change="getDifHours"
                  />
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for="">Cliente</label>
                  <!-- <input
                    type="text"
                    class="form-control"
                    v-model="addData.client"
                  /> -->
                  <select
                    v-model="addData.client"
                    class="form-control"
                    required
                  >
                    <option value="" disabled>/</option>
                    <option
                      v-for="item in adminList"
                      :key="item.v"
                      :value="item.v"
                    >
                      {{ item.v }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for="">Progetto</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="addData.project"
                  />
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for="">Descrizione</label>
                  <textarea
                    class="form-control"
                    v-model="addData.description"
                  ></textarea>
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for="">Ore messo (15min => 0.25)</label>
                  <input
                    type="number"
                    class="form-control"
                    v-model="addData.hours"
                    step="0.25"
                    min="0.25"
                    max="8"
                  />
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label for=""> straordinario </label>
                  <br />
                  <label class="switch">
                    <input type="checkbox" v-model="addData.extraordinary" />
                    <span class="slider round"></span>
                  </label>
                </div>
              </div>

              <div class="col-md-12">
                <div class="form-group">
                  <label for="">User</label>
                  <select
                    v-model="addData.user_id"
                    class="form-control"
                    required
                  >
                    <option value="" disabled>/</option>
                    <option
                      v-for="(item, index) in userList"
                      :key="index"
                      :value="item.k"
                    >
                      {{ item.v }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-success"
              data-dismiss="modal"
              @click="add"
            >
              save
            </button>
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: [],
      data2: [],
      startDate: null,
      endDate: null,
      userId: null,
      userList: [],
      adminList: [],
      hasData: false,
      addData: {
        date: this.getToday(),
        start: this.getStartHour(),
        end: null,
        client: null,
        project: null,
        description: null,
        hours: null,
        extraordinary: 0,
        user_id: null,
      },
      activeItem: "home",
    };
  },
  mounted() {
    //var date = new Date();
    // this.endDate = this.getDate(date, 0);
    // this.startDate = this.getDate(date, -7);
    // console.log(this.startDate);
    // console.log(this.endDate);
    // this.startDate = "2020-12-01";
    // this.endDate = "2020-12-31";
    this.startDate = new Date();
    this.changeMonth(-1);
    this.getUser();
    // this.submit();
  },
  methods: {
    getDate(date, day) {
      date.setDate(date.getDate() + day);
      let year = date.getFullYear();
      let month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      let dd = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return year + "-" + month + "-" + dd;
    },
    changeMonth(n) {
      // console.log("get Month => " + n);
      if (n == 0) {
        this.startDate = new Date();
      }
      var date = new Date(this.startDate),
        y = date.getFullYear(),
        m = date.getMonth();
      // var firstDay = new Date(y, m + n, 1);
      // var lastDay = new Date(y, m + n + 1, 0);
      // console.log("start => " + firstDay);
      // console.log("end => " + lastDay);

      this.startDate = this.getDate(new Date(y, m + n, 1), 0);
      this.endDate = this.getDate(new Date(y, m + n + 1, 0), 0);
      this.submit();
    },
    submit() {
      if (this.userId == null) {
        alert("user non puo' essere vuoto");
      } else {
        // console.log("user_id => " + this.userId);
        var link =
          this.apiURL +
          "/job/list?start=" +
          this.startDate +
          "&end=" +
          this.endDate +
          "&user_id=" +
          this.userId;
        this.$store.dispatch("loading");
        this.axios.get(link).then((r) => {
          this.hasData = r.data.length > 0 ? true : false;
          this.tot(r.data);
          //console.log(this.tot(r.data));
          this.data = r.data;

          this.$store.dispatch("loaded");
        });

        link =
          this.apiURL +
          "/job/detail?start=" +
          this.startDate +
          "&end=" +
          this.endDate +
          "&user_id=" +
          this.userId;
        this.$store.dispatch("loading");
        this.axios.get(link).then((r) => {
          //console.log(this.tot(r.data));
          this.data2 = r.data;

          this.$store.dispatch("loaded");
        });
      }
    },
    add() {
      // console.log(JSON.stringify(this.addData));
      var link = this.apiURL + "/job/add";
      this.$store.dispatch("loading");
      this.axios.post(link, this.addData).then((r) => {
        // this.hasData = r.data.length > 0 ? true : false;
        // this.tot(r.data);
        console.log(r.data);
        // this.data = r.data;

        this.$store.dispatch("loaded");
      });
    },
    getUser() {
      var link = this.apiURL + "/job/user";
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        // this.hasData = r.data.length > 0 ? true : false;
        // this.tot(r.data);
        // console.log("user list => " + JSON.stringify(r.data));
        this.userList = r.data;

        this.$store.dispatch("loaded");
      });
    },
    getAdmin() {
      // console.log("admin");
      if (this.adminList.length == 0) {
        var link = this.apiURL + "/job/admin";
        this.$store.dispatch("loading");
        this.axios.get(link).then((r) => {
          // this.hasData = r.data.length > 0 ? true : false;
          // this.tot(r.data);
          // console.log("admin list => " + JSON.stringify(r.data));
          this.adminList = r.data;

          this.$store.dispatch("loaded");
        });
      }
    },
    getToday() {
      var today = new Date().toISOString().substr(0, 10);
      // console.log("today => " + today);
      return today;
    },
    getStartHour() {
      var today = new Date();
      var h = today.getHours() < 10 ? "0" + today.getHours() : today.getHours();
      var div = parseInt(today.getMinutes() / 15) * 15;
      var m = div < 10 ? "0" + div : div;
      // console.log("time => " + h + ":" + m);
      // this.addData.start = h + ":" + m;
      return h + ":" + m;
    },
    getDifHours() {
      // console.log("diff val => " + this.addData.end + "-" + this.addData.start);
      var start = this.addData.start.split(":");
      var end = this.addData.end.split(":");
      var diff = end[0] - start[0] + ((end[1] - start[1]) / 15) * 0.25;
      // console.log("diff => " + diff);
      this.addData.hours = diff;
    },
    sum(a, b) {
      return parseFloat(a) + parseFloat(b);
    },
    tot(d) {
      var h_ex = 0;
      var h = 0;
      Object.entries(d).forEach(([key, val]) => {
        console.log(key);
        h = parseFloat(h) + parseFloat(val.h);
        //console.log("h[" + key + "] => " + parseFloat(val));
        h_ex = parseFloat(h_ex) + parseFloat(val.h_ex);
      });
      return d.push({ month: "Total", CLIENT: "", h: h, h_ex: h_ex });
    },
    ext(a) {
      return a == 0 ? "No" : "Si";
    },
    isActive(menuItem) {
      return this.activeItem === menuItem;
    },
    setActive(menuItem) {
      this.activeItem = menuItem;
    },
  },
};
</script>
<style>
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>